export default function (Component) {
      if(!Component.options.code) Component.options.code = {};
      Component.options.code['example'] = "class FieldChangeAction extends HistoryPlugin.Action {\n  constructor(prev, next, set) {\n    super()\n    this.prev = prev;\n    this.next = next;\n    this.set = set;\n  }\n  undo() {\n    this.set(this.prev);\n  }\n  redo() {\n    this.set(this.next);\n  }\n}\n\n// внутри метода \"change\" вашего Control (вызывается по действию пользователя)\n// this.value - значение до изменения\n// next - новое значение\n// (v) => this.set(v) - коллбэк для изменения значения поля по undo/redo\nthis.emitter.trigger('addhistory', new FieldChangeAction(this.value, next, (v) => this.set(v)));";
    }