export default function (Component) {
      if(!Component.options.code) Component.options.code = {};
      Component.options.code['custom'] = "import { Action } from 'rete-history-plugin';\n\nexport class YourAction extends Action {\n  constructor() {\n    super();\n  }\n  undo() {\n    ///\n  }\n  redo() {\n    ///\n  }\n}\n\neditor.trigger('addhistory', new YourAction());";
    }